@font-face {
  font-family: 'ignsearch';
  src: url(data:application/vnd.ms-fontobject;base64,);
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),
       url(data:font/woff2;base64,) format('woff2'),
       url(data:font/woff;base64,) format('woff'),
       url(data:font/ttf;base64,) format('truetype'),
       url(data:image/svg+xml;base64,#ignsearch) format('svg');
  font-weight: normal;
  font-style: normal;
}

.m-control.m-container.m-ignsearch [class^="icon-"]:before, .m-control.m-container.m-ignsearch [class*=" icon-"]:before {
  font-family: "ignsearch";
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.m-control.m-container.m-ignsearch .icon-locate:before { content: '\e800'; }
.m-control.m-container.m-ignsearch .icon-ignsearch:before { content: '\e807'; }
.m-control.m-container.m-ignsearch .icon-remove:before { content: '\e93e'; }
.m-panel-btn.icon-ignsearch:before { content: '\e807'; }
